<template>
  <div class="tab_home">
    <div style="background-color: #fafafa; position:relative;">
      <img :src="bg_header"
           style="position:absolute;width:100%; height: 100%"/>
      <div class="user_header">
        <van-row>
          <van-col span="12">
            <img class="user_avatar" :src="portrait" alt="头像" width="55" height="55">
            <div style="line-height: 1">{{name}}</div>
          </van-col>
          <van-col span="12">
            <div>{{balance}}</div>
            <div>账号余额（元）</div>
            <van-button size="small" type="danger" @click="withdraw">提现</van-button>
          </van-col>
        </van-row>
      </div>
    </div>
    <div class="notice">
      <h3>我的任务</h3>
      <div class="tab_home">
      <van-list
        v-model="loading"
        :finished="finished"
        :immediate-check="false"
        @load="loadMore">
        <van-card
          v-for="(goods, goodsI) in items"
          class="order_list--van-card"
          :key="goodsI"
          :thumb="goods.task_pic">
          <div slot="title" class="van-card__row">
            <div class="van-card__title">{{ goods.task_sn }}</div>
            <div class="van-card__price" style="color: #999;">
              {{goods.status_text}}
            </div>
          </div>
          <div slot="desc" class="van-card__row">
            <div class="van-card__desc">{{'佣金 ￥' + goods.gain_money_total}}</div>
            <div class="van-card__num">{{goods.create_time}}</div>
          </div>
          <div slot="footer" class="order_list--footer_btn">
            <van-button size="small" type="danger" @click="toDetails(goodsI)" >查看详情</van-button>
          </div>
        </van-card>
      </van-list>
      <is-empty v-if="isEmpty">暂无任务</is-empty>
      </div>
    </div>

  </div>
</template>

<script>
  import { Tab, Tabs , Panel, Card,Row, Col, Dialog, CellGroup, Cell, Toast, Button, Popup } from "vant";
  import avatar_default from "../../assets/images/logo.png";
  import bg_default from "../../assets/images/home_bg.png";
  import { NOTICES, NOTICES_STATUS } from "@/api/user";
  import {TASK_LIST } from "@/api/task";
  import IsEmpty from "@/vue/components/is-empty/";
  import loadMore from '@/vue/mixin/list-load-more';
  export default {
    name: "index",
    mixins: [loadMore],
    data() {
      return {
        portrait: avatar_default,
        bg_header: bg_default,
        name: "",
        balance: 0,
        items: [],
        show: false,
        content: '',
        invite_auth: '',//邀请权限
        activeIndex : 1,
        currentDate: new Date(),
        showStart : false,
        showEnd : false,
        startDate: '',
        endDate:'',
        isEmpty: false
      };
    },

    created() {
      this.initUser();
    },

    // watch: {
    //   "$route": "resetInit",
    // },

    methods: {
      initUser() {
        //获取用户详情
        this.name = localStorage.name;
        this.invite_auth = localStorage.invite_auth;
        this.resetInit();
        this.initData();
        // this.loadNotices();
      },

      resetInit(){
        let _this = this;
        _this.$user.getUserInfo(function(data){
          _this.balance = data.balance;
        });
      },
      initData(){
        return this.$reqGet(TASK_LIST, {
          per_page: this.pages.perPage,
          page: this.pages.currPage,
          tab_index: this.activeIndex
        }, {
          hideLoading: true
        }).then(res => {
          return this.loadTaskSuccess(res);
        }).catch((err) => {

        })
      },
      loadTaskSuccess(res){
        let list = res.data.data.list;
        if (!list) {
          setTimeout(() => {
            this.isEmpty = true
          }, 500)
          return this.pages;
        }
        this.isEmpty = false
        let page = {
          "pageCount":res.data.data.pageCount,
          "currPage":res.data.data.currPage,
          "perPage":res.data.data.perPage,
          "totalCount":res.data.data.totalCount
        };
        this.items.push(...list);
        return page;
      },
      onStartConfirm(value) {
        this.showStart = false;
        this.startDate = value;
      },
      onEndConfirm(value) {
        this.showEnd = false;
        this.endDate = value;
      },
      toDetails(i){
        let id = this.items[i].id;
        this.$router.push({ name: "taskViews", params: { id: id} })
      },


      loadNotices() {
        this.$reqGet(NOTICES, {
          per_page: 3,
          page: 1
        }).then((res) => {
          this.loadSuccess(res);
        }).catch((err) => {
          this.loadSuccess(err);
        });
      },
      loadSuccess(res) {
        if (!res.data || res.data.code != 0) {
          return;
        }
        let data = res.data.data.list;
        if(data){
          let self = this;
          data.forEach( value => {
            if(value.type == 1 && value.status == 0){
              Dialog.alert({
                title: value.title,
                message: value.content,
              }).then(() => {
                // on close
              });
              // 修改状态
              self.$reqPost(NOTICES_STATUS, {
                noticesId: value.id
              });
            }
            self.items.push(value);
          });

        }

      },
      invite: function() {
        this.$router.push({ name: "invitation" });
      },
      withdraw: function() {
        if (this.balance <= 0) {
          Dialog.alert({
            title: "警告",
            message: "余额不足，无法提现"
          }).then(() => {
            // on close
          });
          return;
        }
        this.$router.push({ name: "withdraw", params: { from: "index" } });
      },
      openNotice: function(notice) {
        if (notice.type == 1) {
          this.show = true;
          this.content = notice.content;
        } else if (notice.type == 2) {
          window.location.href = notice.url;
        }
      },
      linkTo(e) {
        this.$router.push(e);
      },
    },
    watch: {
      //route.push进来后没有执行获取数据的方法可以watch下路由，有变化后再次获取数据，而不是靠刷新一次来获取数据
      $route(){
        this.initData()
      }
    },

    components: {
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
      [Panel.name]: Panel,
      [Card.name]: Card,
      [Col.name]: Col,
      [Row.name]: Row,
      [Dialog.name]: Dialog,
      [CellGroup.name]: CellGroup,
      [Cell.name]: Cell,
      [Toast.name]: Toast,
      [IsEmpty.name]: IsEmpty,
      [Button.name]: Button
    }
  };
</script>

<style lang="scss" scoped>
  .van-search__action {
    padding: 0 10px;
    font-size: 14px;
    line-height: 30px;
    color: #666;
  }
  .order_list {
    background-color: #f9f9f9;
    padding-bottom: 0;
    &--footer_btn {
      text-align: right;
    }
    &--panel{
      margin-bottom: 10px;
    }
    &--van-card{
      background-color: #ffffff;
    }
    &--total{
      text-align: right;
      padding: 10px;
    }
  }
  .van-popup {overflow-y:auto;}
  .user_header {
    position: relative;
    /*background-repeat: no-repeat;*/
    /*background-size: cover;*/
    height: 140px;
    box-sizing: border-box;
    text-align: center;
    // color: #bfbfbf;
    color: #fff;
    padding-top: 30px;
  }

  .tab_home {
    /*background-color: #fafafa;*/
    height:100%;
    .van-col {
      font-size: 13px;
      line-height: 30px;
      text-align: center;
      vertical-align: middle;
      margin-bottom: 10px;
      background-clip: content-box;
    }
  }
  .user_avatar {
    border-radius: 50%;
    padding: 5px;
    border: 3px solid rgba($color:#fff, $alpha: 0.4);
  }

  .notice {
    background-color: #fff;
    margin: 20px 0;
  }

  .bounced {
    width: 70%;
    height: 60%;
    padding: 20px 10px;
    font-size: 12px;
  }
  .code-main {padding:20px;}
  .code-img {float:left;width:90px; height:35px;}
  .code-val {width:120px; height:35px; margin-top:20px; font-size:14px; color:#555; text-align:center;}
  .code-refresh {float:left; display:inline-block; color:#f00; margin-left:10px;}

  h3 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 10px;
    line-height: 2.5;
    margin: 0;

    span:nth-child(2) {
      font-size: 12px;
      color: #666;
      line-height: 3;
      position: relative;
      padding-right: 20px;

      &::after {
        content: "";
        display: inline-block;
        position: absolute;
        right: 0;
        bottom: 50%;
        width: 7px;
        height: 7px;
        border-top: 2px solid #666;
        border-right: 2px solid #666;
        transform: rotate(45deg) translateY(50%);
      }
    }
  }

  .task {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    li {
      width: 20%;
      // height: 50px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      img {
        width: 50px;
        height: 50px;
        margin: 0 auto;
        background-size: 100% 100%;
      }

      p {
        line-height: 2.5;
        text-align: center;
        padding: 0;
        margin: 0;
        font-size: 12px;
        margin-top: 5px;
      }
    }
  }
</style>



